import { TRGBAColor, TRGBColor, TAlpha } from './types';

function toHex(c) {
    const hex = c.toString(16);
    return hex.length === 1 ? `0${hex}` : hex;
}

/**
 * 将rbg转换成rgba
 * @param rbg
 * @param alpha
 */
export function rgb2rgba(rbg: TRGBColor, alpha: TAlpha): TRGBAColor {
    if (rbg === '#00000000') {
        return '#00000000';
    }
    if (alpha > 1 || alpha < 0) {
        throw Error(`alpha=${alpha} illegal`);
    }
    if (rbg && rbg.startsWith('#')) {
        return `#${rbg.substring(1)}${toHex(
            Math.floor(255 * alpha),
        ).toUpperCase()}`;
    }
    return '';
}
